.card {
    display: flex;
    width: 15rem;
    justify-content: space-between;
    padding: 2rem;
    align-items: center;
    border-radius: 1rem;
    margin: 2rem auto;
    box-shadow: 5px 5px 5px 5px;
    max-width: 100%;
}

img {
    max-width: 5rem;
}

@media (min-width: 480px) {
    img {
        width: 6rem;
    }
}